<template>
	<view class="container">
		<u-popup v-model="show" mode="center" width="600rpx" border-radius="14">
			<view class="switch_box">
				<view class="title">您可以切换以下项目：</view>
					<radio-group @change="radioChange" class="radioGroup">
						<view class="radioBox" v-for="(item, index) in identityList" :key="item.value">
							<view>{{item.label}}</view>
							<radio :value="item.value" :checked="item.checked" style="transform:scale(0.7)" color="#03958C" />
						</view>
					</radio-group>
				<view class="footerBtn">
					<text class="cancel" @click="cancel">取消</text>
					<text @click="sure">确定</text>
				</view>
			</view>
		</u-popup>
	</view>
</template>

<script>
	import { bus } from '@/utils/bus.js';
	import httpUtils from '@/utils/httpUtils.js';
	import Tools from '@/utils/tools.js';
	export default {
		data() {
			return {
				show: false,
				identityList:[],
				value: uni.getStorageSync('loginInfo').defaultPrjId,
				nowState:'', // 当前切换的身份 1销售经理
				projectName:'', // 项目label名
			}
		},
		methods: {
			// 获取项目列表
			_getList(){
				let that = this
				Tools.showLoading('加载中')
				httpUtils.sendHttpRequest('2039', Object.assign({}, {defaultPrjId:that.value}), function (res) {
					Tools.hideLoading()
					if (res.data.code == '0') {
						that.show = true
						that.identityList = res.data.data
					}
				})
			},
			// 打开弹窗
			open(data) {
				this.nowState = data
				this._getList()
			},
			// 关闭弹窗
			cancel(){
				this.show = false
			},
			// 确定
			sure(){
				let that = this
				Tools.showLoading('切换中')
				httpUtils.sendHttpRequest('2040', Object.assign({}, {prjId:that.value}), function (res) {
					Tools.hideLoading()
					if (res.data.code == '0') {
						that.identityList.forEach((item,index)=>{
							if(that.value == item.value){
								that.projectName = item.label
							}
						})
						let loginInfo = uni.getStorageSync('loginInfo')
						loginInfo.defaultPrjId = that.value;
						loginInfo.defaultPrjName = that.projectName;
						uni.setStorageSync('loginInfo', loginInfo);
						that.switchSuccess()
						setTimeout(function() {
							if(that.nowState == 1){
								uni.reLaunch({
									url:'/pages/salemanagerVisual/case/index'
								})
							}else{
								uni.reLaunch({
									url:'/pages/salemanVisual/needDeal/index'
								})
							}
						}, 500)
					}
				})
			},
			// 切换成功后调用接口
			switchSuccess(){
				httpUtils.sendHttpRequest('2043', Object.assign({}, ''), function (res) {
					if (res.data.code == '0') {
						Tools.showSuccessToast(res.data.msg)
					}
				})
			},
			// 选中某个单选框时，由radio时触发
			radioChange(e) {
				this.value = e.detail.value
			},
		}
	}
</script>

<style lang="scss">
	@import '@/styles/poup.scss';
</style>
